* {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  outline: none;
}
.login {
  display: flex;
  background-color: #2b3137;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
}
.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: white;
  box-sizing: border-box;
  padding: 30px 40px;
  min-height: 60vh;
  border-radius: 10px;
  width: 30%;
  margin: auto;
  margin-top: 5%;
}
.header {
  padding: 30px 0 30px 0;
  text-align: center;
}
.header h1 {
  font-size: 21px;
}
.input_area {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-content: center;
  text-align: center;
}
.input_area input {
  /* width: 100%; */
  margin: 10px 0;
  border: 1px grey solid;
  border-radius: 20px;
  padding: 5px 10px;
  font-size: 12px;
  outline: none;
  color: grey;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.actions {
  text-align: center;
  padding: 30px 0 10px 0;
}
.actions button {
  background-color: #1aebb6;
  border: none;
  border-radius: 20px;
  padding: 5px 25px;
  cursor: pointer;
}
.actions p {
  font-size: 13px;
}
.Sign_up_link {
  font-weight: 500;
  color: #14b397;
  text-decoration: none;
}
